import Image from "next/image";
import product from "../../public/assets/products/24913.jpg"
export default function Home() {
  return (
        <div className="">
          <Image
            src={product} // Correct path to public folder
            alt="Product Image"
           // Example height (adjust as needed)
          />
        </div>
  );
}
export default function Home() {
  return (
    <div className="grid grid-rows-[20px_1fr_20px] items-center justify-items-center min-h-screen p-8 pb-20 gap-16 sm:p-20 font-[family-name:var(--font-geist-sans)]">
      <div className="w-full mt-56 flex justify-between">
        <div className="h- w-64">
          {/* Use Next.js Image for optimized rendering */}
          <Image
            className="h-72"
            src="/assets/products/24913.jpg" // Correct path to public folder
            alt="Product Image"
            width={508} // Example width (adjust as needed)
            height={200} // Example height (adjust as needed)
          />
        </div>
      </div>
    </div>
  );
}
